.container{
    justify-content: center;
    transition: 0.5s;
    display: flex;
    flex-direction: column;
}
.divbtn{
   
    font-size: 6vw;
    padding: 0.7em;
    color: white;
    
}
.divbtn span{
    transition: 0.5s;
    border-width: 1vw;
    padding: 1vw 2vw;
 
    border-style: solid;
    border-color: #6B7AE5;
    border-radius: 30px;
}
.divbtn span:hover{
    border-color: white;
    
    transition: 0.5s;
    cursor: pointer;
    font-size: 6.5vw;
}