.container{
    background: rgb(29,28,62);
    background: linear-gradient(180deg, rgba(29,28,62,1) 0%, rgba(43,45,93,1) 65%, rgba(43,45,93,1) 87%);
    margin: 0;
    padding: 50px;
    width: 100%;
    font-size: 10px;
    display: flex;
    justify-content: center;
    flex-direction: column;
}
.containercontato{
    justify-content: center;
    display: flex;
    flex-direction: column;
   
   
}

.fonecontainer{
    display: flex;
    font-size: 0.5em;
    
    justify-content: center;
    
}

.emailcontainer{
    display: flex;
   font-size: 0.5em;
   justify-content: center;
}
.loja{
    display: flex;
    justify-content: center;
    color: rgb(255, 255, 255);

}
.googleplay p:hover{
    transition: 0.5s;
    color: #6B7AE5;
    cursor: pointer;
}
.appstore p:hover{
    transition: 0.5s;
    color: #6B7AE5;
    cursor: pointer;
}
.containerlogo{
    width: 100%;

}
.containerlogo img{
    width: 100%;
    
}
.containercontent{
    width: 100%;
    display: flex;
    justify-content: space-around;
    
}

.redes p{
    font-size: 2vw;
}
.redes{
    
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-size: 4em;
    color: white;
}
.containerredes{
    display: flex;
    justify-content: center;
}
.containerinstagram{
    padding: 10px;
   
}
.containerinstagram:hover {
    transition: 0.5s;
    color: #6B7AE5;
    cursor: pointer;
}
.containerlinkedin:hover{
    transition: 0.5s;
    color: #6B7AE5;
    cursor: pointer;
}
.containerlinkedin{
    padding: 10px;
}
.containerinstagram a:hover{
    color: purple;
}
.logo{
    display: flex;
    justify-content: center;
    padding: 1em;
    width: 100%;
}
.logo img{
    max-width: 200px;
    width: 100%;
}
.fonecontainer p:hover{
    transition: 0.5s;
    color: #6B7AE5;
    -webkit-text-stroke: 0.4px  #6B7AE5;
}
.fonecontainer p:hover #abner{
    transition: 0.5s;
    color: #6B7AE5;
    -webkit-text-stroke: 0.4px  #6B7AE5;
}

