.bannercontainer{
    width: 100%;
    position: relative;
    margin: 0px;
    justify-content: center;
  
}
.titlecontainer{
    width: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    position: absolute;
    top: 30%;
    font-size: 1vw;
}
.btncontainer{
    font-family: ProximaNovaExtraBold;
    width: 100%;
    
}
.btncontainer button{
    border-style: none;
    font-family: ProximaNovaExtraBold;
    padding: 0.5em 1em;
    box-shadow: 0px 8.37838px 16.7568px rgba(136, 144, 194, 0.2), 0px 20.9459px 62.8378px rgba(255, 255, 255, 0.15);
    border-radius: 16.7568px;
    background: #6B7AE5;
    color: white;
    font-size: 3em;
  
}
.btncontainer button:hover{
    transition: 1s;
    background-color: #1D1C3E;
    border: 4px solid white;
    cursor: pointer;
    box-shadow: 0px 8.37838px 16.7568px rgba(136, 144, 194, 0.2), 0px 20.9459px 62.8378px #6B7AE5;
}
.text{
    padding: 1em;
    font-size: 3em;

}
.titlecontainer span{
    color: #6B7AE5;
    -webkit-text-stroke: 0px;
}
.rub{
    font-family: ProximaNovaExtraBold;
}
.bank{
    font-family: ProximaNovaThin;
}
.container{
    padding: 0;
    margin: 0;
}
.bannercontainer img{
    width: 100%;
}