.container img{
    width: 100%;
   
}
.container{
    position: relative;
    height: 100%;
}
.containercontent{
    
    display: flex;
    justify-content: center;
    flex-direction: column;
    top: 2%;
    position: absolute;
}
.containercelulares{
    justify-content: center;
    display: flex;
    
}
.containercelulares img{
    max-width: 450px;
    width: 100%;
    
}
.title{
    font-size: 1.7vw;
    -webkit-text-stroke: 0px;
}
.text{
    padding: 4em;
    font-size: 1.5vw;
    -webkit-text-stroke: 0px;
    width: 100%;
    text-align: center;
    display: flex;
    justify-content: center;
}
.text p{
    width: 90%;
    font-family: ProximaNovaThin;

}
.rub{
    font-family: ProximaNovaExtraBold;
    color: #6B7AE5;
    -webkit-text-stroke: 0px;
}
.bank{
    color: #6B7AE5;
    font-family: ProximaNovaThin;
    -webkit-text-stroke: 0px;
}
.containerbtn{
   
    padding: 1em;
    display: flex;
    justify-content: center;
}
.containerbtn button{
    transition: 0.5s;
    font-family: ProximaNovaExtraBold;
    width: 60%;
    height: 15vw;
    font-size: 7vw;
    color: white;
    background-color: #6B7AE5;
    border-style: none;
    box-shadow: 0px 3.13514px 6.27027px rgba(136, 144, 194, 0.2), 0px 7.83784px 23.5135px rgba(255, 255, 255, 0.15);
    border-radius: 6.27027px;
    
}
.containerbtn button:hover{
    transition: 0.5s;
    cursor: pointer;
    background-color: #1D1C3E;
    color: white;
    box-shadow: 0px 3.13514px 6.27027px rgba(136, 144, 194, 0.2), 0px 7.83784px 23.5135px rgba(9, 37, 131, 0.15);

}

