.container{
   
    
    display: flex;
    flex-direction: column;
    align-items: center;
    background-size: 100%;
}
.bannercontainer{
    width: 100%;
    position: relative;
    font-size: 20px;
}
.textobanner{
    text-align: center;
    position: absolute;
    width: 60%;
    top:  20%;
    left: 37%;
    font-size: 1.5vw;
}
.bannercontainer img{
    width: 100%;
}
.containerbtn{
    padding-top: 30px;
}
.textobanner button{
    padding: 20px 40px;
    color: white;
    border-radius: 550.451px;
    background: #1D1C3E;
    box-shadow: 0px 1.65301px 4.40801px rgba(136, 144, 194, 0.25), 0px 3.85701px 11.571px rgba(245, 245, 245, 0.2);
    font-size: 3em;
    border: none;
}
.button:hover{
    transition: 1s;
    background-color: #6B7AE5;
    cursor: pointer;
}
p{
    
    
    color: white;
    font-size: 4em;
    margin: 0;
    text-shadow: 0px 1.58224px 19.778px #1D1C3E;
    -webkit-text-stroke: 0.4px rgb(255, 255, 255);
}
