.App {
  text-align: center;
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: ProximaNova, 'Courier New', Courier, monospace;
  
}
@font-face {
  font-family: 'ProximaNova';
  src: url('./fonts/Proxima\ Nova\ Font.otf') format(opentype);
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'ProximaNovaThin';
  src: url('fonts/FontsFree-Net-Proxima-Nova-Thin.otf') format(opentype);
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'ProximaNovaExtraBold';
  src: url("fonts/FontsFree-Net-Proxima-Nova-Xbold.otf") format(opentype);
  font-weight: normal;
  font-style: normal;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
