
.container{
    background-color: #1D1C3E;
    display: flex;
    align-items: center;
    padding: 1em;
    justify-content: space-between;
}

.containerimg{
    
   
   
    display: flex;
    
}
.navbarbtn button{
   padding: 10px 40px;
   border-radius: 999px;
   border-style: none;
   background: #6B7AE5;
   color: white;
    
}

.navbarbtn ul{
    display: flex;
    list-style: none;
}
.navbarbtn ul li{
   padding: 0.5em;
}
.containerimg img{
    padding-left: 1em;
    width: 100%;
    max-width: 180px;
    min-width: 100px;
    
}
