.container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2em;
}

.containerlogo img{
    max-width: 250px;
    width: 100%;
}
.containerhamburguer{
    display: flex;
    justify-content: end;
    flex-direction: column;
    background-color: black;
   
}

    
.containerhamburguer button{
    cursor: pointer;
    justify-content: center;
    display: flex;
    background-color: #1D1C3E;
    width: 100%;
    border-style: none;
    background-color: none;
   
}
.containerhamburguer button img{
    width: 100%;
    max-width: 70px;
}