.container{
    padding: 1em;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
   
}
.containerdescricao{
    padding: 1em;
}
.container p{
    font-size: 3vw;
}
.containertitle{
    padding: 1em;
}
.card:hover{
    transition: 0.5s;
    height: 21vw;
}

.card{
    transition: 0.5s;
    width: 100%;
    height: 20vw;
    align-items: center;
    display: flex;
    justify-content: center;
    flex-direction: column;
    border-style: solid;
    border-color: white;
    box-shadow: 0px 3.95683px 49.4604px rgba(255, 255, 255, 0.25);
    border-radius: 28.687px;
    background: #1D1C3E;
}
.card p{
    width: 100%;
    font-size: 1.5vw;
    flex-wrap: wrap;
}
.containerimg{
 
    padding: 1em;
    width: 50%;
}