.container img{
    width: 100%;
}

.container{
    position: relative;
}
.content{
    top: 18%;
    position: absolute;
    display: flex;
    flex-direction: column;
}
.content p{
    font-size: 8vw;
}
.containerbtn{
    padding: 1em;
}
.content button{
    transition: 0.5s;
    padding: 2vw 6vw;
    color: white;
    border-radius: 550.451px;
    background: #1D1C3E;
    box-shadow: 0px 1.65301px 4.40801px rgba(136, 144, 194, 0.25), 0px 3.85701px 11.571px rgba(245, 245, 245, 0.2);
    font-size: 4vw;
    border: none;
}
.button:hover{
    transition: 1s;
    background-color: #6B7AE5;
    cursor: pointer;
}