.bannercontainer{
    width: 100%;
    position: relative;
    justify-content: center;
}
.bannercontainer img{
    width: 100%;
}
.containercontent{
  
    width: 40vw;
    position: absolute;
    display: flex;
    flex-direction: column;
    right: 5%;
    top: 27%;
}
.containercontent p{
    font-size: 4vw;
}
.containerbtn{
    padding: 1em;
}
.containerbtn button{
    background: #6B7AE5;
    color: white;
    font-size: 3vw;
    font-family: ProximaNovaExtraBold;
    width: 25vw;
    height: 10vw;
    box-shadow: 0px 8.37838px 16.7568px rgba(136, 144, 194, 0.2), 0px 20.9459px 62.8378px rgba(255, 255, 255, 0.15);
    border-radius: 16.7568px;
    border-style: none;
}
.containerbtn button:hover{
    transition: 1s;
    background: #1D1C3E;
    color: white;
    box-shadow: 0px 8.37838px 16.7568px rgba(136, 144, 194, 0.2), 0px 20.9459px 62.8378px rgba(255, 255, 255, 0.15);
}

